body {
  margin: 0;
}

nav a {
  color: #060b35;
  transition: color .3s;
}

nav a:hover {
  color: #00acdc;
}

nav a.is-active {
  font-weight: bold;
}

.card {
  width: 200px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__title {
  transition: color .3s;
}

.card:hover .card__title {
  color: #34568b;
}

.card__button {
  transition: background-color .3s, color .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.catalog {
  grid-template-columns: 1fr;
  gap: 48px 46px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 1fr);
  }
}
